.wave {
  background: #fff;
  height: 65px;
  position: relative;
}

.wave:before,
.wave::after {
  border-bottom: 5px solid #f6f6f6;
}

.wave::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 10px;
  background-size: 20px 40px;
  background-image: radial-gradient(circle at 10px -15px, transparent 20px, #f6f6f6 21px);
}

.wave::after {
  content: "";
